/**
 * TextInput.scss
 * CSS for custom form dropdown menu with input
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
 @import '../../../base';

.text-input-container {
  width: 100%;
  border-radius: $border-radius-main;
  background-image: linear-gradient($color-form, $color-form);
  background-origin: border-box;
  background-clip: content-box, border-box;
  border: solid 2px transparent;
  padding: 0;
  // margin: 10px 0;

  .text-input {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;

    .text {
      flex: 1;
      width: 100%;
      margin: auto 20px;
      border: none;
      background-color: transparent;
      font-size: $font-size-regular;
      font-weight: $font-weight-semibold;
      color: $color-title;

      &::placeholder {
        color: $color-line;
      }

      &:focus {
        outline: none;
      }
    }
  }

  .text-area {
    padding: 15px;
    height: 100%;
      width: 100%;
    textarea {
      height: 100%;
      width: 100%;
  
      background-color: transparent;
      border: none;
      overflow: auto;
      outline: none;
  
      -webkit-box-shadow: none;
      -moz-box-shadow: none;
      box-shadow: none;
  
      resize: none;

      font-size: $font-size-regular;
      font-weight: $font-weight-normal;
      color: $color-title;

      &::placeholder {
        color: $color-line;
      }

      &:focus {
        outline: none;
      }
    }
  }

  .postcode {
    margin: 0 20px;
    display: flex;
    align-items: center;
    &:hover {
      img {
        filter: $color-filter-hover-alt;
      }
    }

    &:active {
      img {
        filter: $color-filter-hover;
      }
    }
  }
}

.text-input-container:hover,
.text-input-container:focus,
.focus, {
  transition: all 0.3s ease;
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.15);
  background-image: linear-gradient($color-form, $color-form),
    linear-gradient(101deg, $color-hover, $color-body);
}

