.subscribe-section .promotion-box .title-sec, .subscribe-section .promotion-box .title-sec .title-desc {
  font-family: "Open Sans", sans-serif;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.1px;
  text-align: center;
  color: white; }

.subscribe-section {
  padding: 55px 30px; }
  @media screen and (max-width: 767px) {
    .subscribe-section {
      padding-top: 0; } }
  .subscribe-section .wrapper {
    padding: 0;
    display: flex; }
    @media screen and (min-width: 990px) {
      .subscribe-section .wrapper {
        padding: 0 55px !important; } }
    .subscribe-section .wrapper .subscribe-form-hidden {
      display: none; }
    .subscribe-section .wrapper .phone-container {
      width: 100%;
      position: relative;
      min-height: 200px; }
      @media screen and (min-width: 990px) {
        .subscribe-section .wrapper .phone-container {
          width: 50%;
          height: 530px !important; } }
      @media screen and (min-width: 768px) {
        .subscribe-section .wrapper .phone-container .phone-inner {
          margin: 80px 0 0 0; } }
      .subscribe-section .wrapper .phone-container .phone-inner .phone-image {
        position: relative;
        z-index: -1; }
        @media screen and (min-width: 768px) {
          .subscribe-section .wrapper .phone-container .phone-inner .phone-image {
            transform: translateX(-50px) translateY(-120px); } }
        @media screen and (min-width: 1250px) {
          .subscribe-section .wrapper .phone-container .phone-inner .phone-image {
            transform: translateX(calc(10vw)) translateY(-120px); } }
        @media screen and (min-width: 1450px) {
          .subscribe-section .wrapper .phone-container .phone-inner .phone-image {
            transform: translateX(calc(80px + 10vw)) translateY(-120px); } }
        .subscribe-section .wrapper .phone-container .phone-inner .phone-image img {
          max-height: 450px; }
          @media screen and (max-width: 767px) {
            .subscribe-section .wrapper .phone-container .phone-inner .phone-image img {
              max-height: 320px;
              transform: translateX(-50px); } }
  .subscribe-section .promotion-box {
    width: 100%;
    padding: 20px; }
    @media screen and (min-width: 900px) {
      .subscribe-section .promotion-box {
        width: 50%;
        padding: 20px; } }
    .subscribe-section .promotion-box .p-20:nth-child(1) {
      margin: 20px auto 0 auto; }
      @media screen and (min-width: 990px) {
        .subscribe-section .promotion-box .p-20:nth-child(1) {
          width: 100%;
          margin: 20px 0 0 0; } }
    .subscribe-section .promotion-box .p-20:nth-child(2) {
      margin-top: 55px; }
      @media screen and (min-width: 990px) {
        .subscribe-section .promotion-box .p-20:nth-child(2) {
          width: 100%;
          margin: 55px auto 0 auto; } }
    .subscribe-section .promotion-box .p-20:nth-child(3) {
      margin-top: 37px; }
      @media screen and (min-width: 990px) {
        .subscribe-section .promotion-box .p-20:nth-child(3) {
          width: 100%;
          margin: 37px auto 0 auto; } }
    .subscribe-section .promotion-box .input-wrapper-main {
      width: 100%;
      /*.text {
        font-size: 14px;
        margin: 0 20px !important;
        width: 40px;

        @media screen and(min-width: 1000px){
          font-size: 18px;
          width: 120px;
        }
      }*/ }
      @media screen and (min-width: 1200px) {
        .subscribe-section .promotion-box .input-wrapper-main {
          width: 80%; } }
      .subscribe-section .promotion-box .input-wrapper-main button {
        min-width: 80;
        max-height: 46px; }
        @media screen and (min-width: 990px) {
          .subscribe-section .promotion-box .input-wrapper-main button {
            min-width: 130px;
            max-height: 48px; } }
        .subscribe-section .promotion-box .input-wrapper-main button p {
          visibility: hidden; }
          .subscribe-section .promotion-box .input-wrapper-main button p:after {
            content: 'Subscribe';
            visibility: visible;
            margin-left: -50px;
            font-size: 14px;
            font-weight: 600;
            color: white; }
            @media screen and (min-width: 1000px) {
              .subscribe-section .promotion-box .input-wrapper-main button p:after {
                font-size: 18px; } }
    .subscribe-section .promotion-box .title {
      font-size: 30px;
      font-family: "After-Night", sans-serif;
      color: #354F53; }
      @media screen and (min-width: 450px) {
        .subscribe-section .promotion-box .title {
          font-size: 36px; } }
    .subscribe-section .promotion-box .title-sec {
      text-align: left;
      font-size: 18px;
      font-weight: 800;
      color: #354F53; }
      @media screen and (min-width: 1440px) {
        .subscribe-section .promotion-box .title-sec {
          font-size: 24px; } }
      .subscribe-section .promotion-box .title-sec .title-desc {
        text-align: left;
        width: 60%;
        font-size: 16px;
        color: #46737A; }
        @media screen and (min-width: 1440px) {
          .subscribe-section .promotion-box .title-sec .title-desc {
            font-size: 18px; } }
    .subscribe-section .promotion-box .download-now a {
      width: 40%;
      float: left;
      margin-right: 15px;
      margin-bottom: 15px; }
      @media screen and (min-width: 990px) {
        .subscribe-section .promotion-box .download-now a {
          width: 20% !important;
          margin-right: 20px !important;
          margin-bottom: 0px; } }
    .subscribe-section .promotion-box .download-now img {
      max-width: 150px;
      width: 100%; }
